.container {
  font-family: SourceSansPro, Arial, sans-serif;
  margin: -31px -30px 0px;
}

.topContainer{
  background-image: linear-gradient(90deg, rgb(0, 26, 87), rgb(246, 234, 184));
  color: #fff;
}

h1 {
  font-family: OpenSans,Arial,sans-serif;
}

.topWrapper {
  padding: 20px 60px 30px; 
}

sup {
  color: red;
}

.wrapper {
  padding: 20px 50px 0px; 
 }

.form {
  margin: 30px;
  background-color: #fbfbf8;
  border-radius: 4px; 
  padding: 20px 40px;
  overflow-y: auto;
  display: block;
}

.form div {
  padding: 10px;
  margin-left: 20px;
  font-size: 20px;
}

.name, .education, .college, .reason {
  position: relative;
  margin: 10px 10px 10px 12px;
  min-height: 25px;
  min-width: 300px;
}
.education, .reason {
  padding: 1px 2px;
  height:30px;
}

.email {
  position: relative;
  margin: 10px 10px 10px 45px;
  min-height: 25px;
  min-width: 300px;
}

.phone{
  position: relative;
  margin: 10px;
  min-height: 25px;
  min-width: 300px;
}

.certificate {
  margin: 0px 5px 0px 10px;
}

.registerButton, .cancelButton {
  margin-left: 40px;
  border: 1px solid #4085d3;
  background-color: #4085d3;
  height: 35px;
  font-size: 16px;
  color: white;
  border: 0;
  padding: 10px;
  min-width: 100px;
}

.congras {
  display: block;
  text-align: center;
  color: #29303b;
  font-weight: 500;
  font-size: 20px;
  margin-top: 30px;
  
}

.confirmContainer {
  width: 500px;
  margin: 3% auto 3%;
  padding: 10px 60px 40px;
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 18px;
}

.confirmContainer h4{
  text-align: center;
}
